import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import Chartjs from 'chart.js';

class Chart extends Component {

  initializeChart(result) {
    const backgroundColor = [
                    'rgba(255, 99, 132, 0.2)',
                    'rgba(54, 162, 235, 0.2)',
                    'rgba(255, 206, 86, 0.2)',
                    'rgba(75, 192, 192, 0.2)',
                    'rgba(153, 102, 255, 0.2)',
                    'rgba(255, 159, 64, 0.2)'
                ];

    const borderColor = [
                    'rgba(255,99,132,1)',
                    'rgba(54, 162, 235, 1)',
                    'rgba(255, 206, 86, 1)',
                    'rgba(75, 192, 192, 1)',
                    'rgba(153, 102, 255, 1)',
                    'rgba(255, 159, 64, 1)'
                ];
    const backgroundColorLength = backgroundColor.length;
    const borderColorLength = borderColor.length;
    const dataLength = result.data.length;

    for (let x = 0; x < dataLength; x++) {
        backgroundColor[x] = backgroundColor[x % backgroundColorLength];
        borderColor[x] = borderColor[x % borderColorLength];
    }

    const el = ReactDOM.findDOMNode(this);
    const ctx = el.getContext('2d');
    const chart = new Chartjs(ctx, {
        type: 'bar',
        data: {
            labels: Object.keys(result.data),
            datasets: [{
                label: 'Random Numbers',
                data: result.data,
                backgroundColor,
                borderColor,
                borderWidth: 1
            }]
        },
        options: {
            scales: {
                yAxes: [{
                    ticks: {
                        beginAtZero:true
                    }
                }]
            }
        }
    });

    this.setState({chart});
  }
  componentDidMount() {
    const { result } = this.props;
    this.initializeChart(result);
  }
  componentWillReceiveProps(nextProps) {
    this.distroyChart();
    const { result } = nextProps;
    this.initializeChart(result);
  }
  componentWillUnmount() {
    this.distroyChart();
  }
  distroyChart() {
    if (this.state && this.state.chart) {
      this.state.chart.destroy();
      this.setState({chart: null});
    }
  }
  render() {
    return (
          <canvas id="myChart" width="400" height="400"></canvas>
    );                
  }
}

export default Chart;




