import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import RandomNumberForm from './components/RandomNumberForm/RandomNumberForm';
import Chart from './components/Chart/Chart';
import NumberList from './components/NumberList/NumberList';
import superagent from 'superagent';
import Loading from './components/Loading/Loading';

class App extends Component {
  constructor(props) {
    super(props);
    this.data10 = {"type":"uint8","length":10,"data":[51,20,16,176,220,50,168,169,112,65],"success":true};
    this.data5 = {"type":"uint16","length":5,"data":[7633,57342,48935,4198,17651],"success":true};
    this.state = {...this.data10};
  }
  handleRequest = (value) => {

    this.setState({...this.state, loading: true});

    superagent
      .get('https://qrng.anu.edu.au/API/jsonI.php')
      .query(value)
      .end((err, res) => {
        const result = JSON.parse(res.text);
        this.setState({...result, loading: false});
      });
  };

  render() {
    return (
      <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>Random Number Chart</h2>
        </div>
        <div className="row">
          <div className="col col-md-5"></div>
          <div className="col col-md-2 well">
            { this.state.loading ? <Loading /> : <RandomNumberForm result={this.state} handleRequest={this.handleRequest}/>}
          </div>
          <div className="col col-md-5"></div>
        </div>
        <div className="row">
          <div className="col col-md-2"></div>
          <div className="col col-md-4">
            { this.state.loading ? <Loading /> : <Chart result={this.state} />}
          </div>
          <div className="col col-md-4">
            <NumberList result={this.state}/>
          </div>
          <div className="col col-md-2"></div>
        </div>
      </div>
    );
  }
}

export default App;



