import React, { Component } from 'react';
import './RandomNumberForm.css';

class RandomNumberForm extends Component {
  handleRequest =(event) => {
    event.preventDefault();
    this.props.handleRequest({
      length: this.refs.length.value,
      type: this.refs.type.value
    }
  )}

  render() {
    const { result } = this.props;
    return (
      <form className="RandomNumberForm" onSubmit={this.handleRequest}>
        <div className="input-group">
          <span className="label label-default">Number Length</span>
          <input ref="length" type="text" className="form-control" id="basic-url" defaultValue={result.length} />
        </div>
        <br/>
        <span className="label label-default">Number Type</span>
        <select className="form-control" ref="type" defaultValue={result.type} >
          <option value="uint8">type: uint8</option>
          <option value="uint16">type: uint16</option>
        </select>
        <button className="btn btn-primary" disabled={result.loading}>request</button>
      </form>
    );                  
  }

}

export default RandomNumberForm;



